import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Progress } from '@/components/ui/progress';
import { Wallet, Plus, Edit, Trash2, Phone, DollarSign, X, Settings, Info, Star, StarOff, CreditCard, TrendingUp, TrendingDown, Save, IdCard, Eye, Printer, ArrowLeft, User } from 'lucide-react';
import { MasterPinDialog } from '@/components/MasterPinDialog';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { SubscriptionPlan } from '@/utils/subscriptionService';
import { merchantWalletService, transactionService } from '@/integrations/firebase/services';
import { walletSelectionPinService } from '@/utils/walletSelectionPinService';
import { walletDailyLimitsService } from '@/utils/walletDailyLimitsService';
import WalletsViewPage from './WalletsViewPage';
import WalletDetailsPage from './WalletDetailsPage';

interface Wallet {
  id: string;
  name: string;
  phone: string;
  ownerName?: string; // اسم مالك المحفظة
  nationalId?: string; // الرقم القومي للخط
  isDefault: boolean;
  monthlyWithdrawalLimit: number;
  monthlyTransferLimit: number;
  monthlyWithdrawalUsage?: number; // المبلغ المسحوب هذا الشهر
  monthlyTransferUsage?: number; // المبلغ المحول هذا الشهر
  dailyWithdrawalLimit: number; // الحد الأقصى للسحب اليومي
  dailyTransferLimit: number; // الحد الأقصى للتحويل اليومي
  dailyWithdrawalUsage?: number; // المبلغ المسحوب اليوم
  dailyTransferUsage?: number; // المبلغ المحول اليوم
  lastResetDate?: string; // تاريخ آخر إعادة تعيين للعدادات
  walletProvider?: string; // شركة المحفظة
}

interface WalletManagementProps {
  isOpen: boolean;
  onClose: () => void;
  onWalletUpdate?: () => void;
  initialEditingWallet?: Wallet | null;
}

// بيانات شركات المحفظة
const walletProviders = [
  {
    id: 'vodafone',
    name: 'فودافون كاش'
  },
  {
    id: 'etisalat',
    name: 'اتصالات كاش'
  },
  {
    id: 'orange',
    name: 'أورنج كاش'
  },
  {
    id: 'we_pay',
    name: 'وي باي'
  },
  {
    id: 'smart_wallet',
    name: 'سمارت ولت'
  },
  {
    id: 'access_pay',
    name: 'أكسس باي'
  },
  {
    id: 'insta_pay',
    name: 'انستا باي'
  }
];

const WalletManagement: React.FC<WalletManagementProps> = ({ isOpen, onClose, onWalletUpdate, initialEditingWallet }) => {
  const { toast } = useToast();
  const { user, userSubscription } = useFirebaseAuth();
  const isTahweelaPlan = (() => {
    const raw = (userSubscription as any)?.subscription?.planType ?? (userSubscription as any)?.subscription?.plan ?? (userSubscription as any)?.planType ?? (userSubscription as any)?.plan ?? null;
    const s = typeof raw === 'string' ? String(raw).trim().toLowerCase() : null;
    return raw === SubscriptionPlan.TAHWEELA || s === 'tahweela' || s === 'تحويله';
  })();
  const navigate = useNavigate();
  const [wallets, setWallets] = useState<Wallet[]>([]);
  const [isAddingWallet, setIsAddingWallet] = useState(false);
  const [editingWallet, setEditingWallet] = useState<Wallet | null>(null);
  const [walletName, setWalletName] = useState('');
  const [walletPhone, setWalletPhone] = useState('');
  const [walletOwnerName, setWalletOwnerName] = useState('');
  const [walletNationalId, setWalletNationalId] = useState('');
  const [walletProvider, setWalletProvider] = useState('');
  const [monthlyWithdrawalLimit, setMonthlyWithdrawalLimit] = useState('200000');
  const [monthlyTransferLimit, setMonthlyTransferLimit] = useState('200000');
  const [dailyWithdrawalLimit, setDailyWithdrawalLimit] = useState('100000');
  const [dailyTransferLimit, setDailyTransferLimit] = useState('60000');
  const [isDefault, setIsDefault] = useState(false);
  const [showProviderInfo, setShowProviderInfo] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [showWalletsView, setShowWalletsView] = useState(false);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState(false);
  const [selectedWallet, setSelectedWallet] = useState<Wallet | null>(null);

  // دالة للحصول على مفتاح localStorage المرتبط بالمستخدم
  const getWalletsKey = () => `wallets_${user?.uid || 'default'}`;

  // افتح نموذج التعديل مباشرة إذا تم تمرير محفظة للتعديل
  useEffect(() => {
    if (initialEditingWallet) {
      setEditingWallet(initialEditingWallet);
      setIsAddingWallet(true);
      setWalletName(initialEditingWallet.name || '');
      setWalletPhone(initialEditingWallet.phone || '');
      setWalletOwnerName(initialEditingWallet.ownerName || '');
      setWalletNationalId(initialEditingWallet.nationalId || '');
      setWalletProvider(initialEditingWallet.walletProvider || '');
      setMonthlyWithdrawalLimit((initialEditingWallet.monthlyWithdrawalLimit || 200000).toString());
      setMonthlyTransferLimit((initialEditingWallet.monthlyTransferLimit || 200000).toString());
      setDailyWithdrawalLimit((initialEditingWallet.dailyWithdrawalLimit || 100000).toString());
      setDailyTransferLimit((initialEditingWallet.dailyTransferLimit || 60000).toString());
      setIsDefault(!!initialEditingWallet.isDefault);
    }
  }, [initialEditingWallet]);

  // دالة لفحص وإعادة تعيين العدادات الشهرية
  const checkAndResetMonthlyUsage = (wallet: Wallet): Wallet => {
    const currentDate = new Date();
    const currentMonth = currentDate.getMonth();
    const currentYear = currentDate.getFullYear();
    
    if (!wallet.lastResetDate) {
      // إذا لم يكن هناك تاريخ إعادة تعيين، قم بتعيين التاريخ الحالي
      return {
        ...wallet,
        monthlyWithdrawalUsage: wallet.monthlyWithdrawalUsage || 0,
        monthlyTransferUsage: wallet.monthlyTransferUsage || 0,
        lastResetDate: currentDate.toISOString().split('T')[0]
      };
    }
    
    const lastResetDate = new Date(wallet.lastResetDate);
    const lastResetMonth = lastResetDate.getMonth();
    const lastResetYear = lastResetDate.getFullYear();
    
    // إذا كان الشهر أو السنة مختلفة، قم بإعادة تعيين العدادات
    if (currentMonth !== lastResetMonth || currentYear !== lastResetYear) {
      return {
        ...wallet,
        monthlyWithdrawalUsage: 0,
        monthlyTransferUsage: 0,
        lastResetDate: currentDate.toISOString().split('T')[0]
      };
    }
    
    return wallet;
  };

  // دالة لحساب الاستخدام الشهري للمحفظة من Firebase (تستخدم مخطط المعاملة الحالي)
  const calculateWalletMonthlyUsage = async (walletId: string, transactions: any[]) => {
    const currentMonth = new Date().getMonth();
    const currentYear = new Date().getFullYear();
    
    const monthlyTransactions = transactions.filter((t: any) => {
      const transactionDate = new Date(t.createdAt);
      return (
        transactionDate.getMonth() === currentMonth && 
        transactionDate.getFullYear() === currentYear &&
        t.lineId === walletId
      );
    });

    // "receive" = عمليات السحب من المحفظة، "send" = عمليات التحويل من المحفظة
    const withdrawalUsage = monthlyTransactions
      .filter((t: any) => t.txnType === 'receive')
      .reduce((sum: number, t: any) => sum + (t.amount || 0), 0);

    const transferUsage = monthlyTransactions
      .filter((t: any) => t.txnType === 'send')
      .reduce((sum: number, t: any) => sum + (t.amount || 0), 0);

    return { withdrawalUsage, transferUsage };
  };

  // دالة لحساب الاستخدام اليومي للمحفظة (تستخدم مخطط المعاملة الحالي)
  const calculateWalletDailyUsage = async (walletId: string, transactions: any[]) => {
    const today = new Date();
    const currentDay = today.getDate();
    const currentMonth = today.getMonth();
    const currentYear = today.getFullYear();
    
    const dailyTransactions = transactions.filter((t: any) => {
      const transactionDate = new Date(t.createdAt);
      return (
        transactionDate.getDate() === currentDay &&
        transactionDate.getMonth() === currentMonth && 
        transactionDate.getFullYear() === currentYear &&
        t.lineId === walletId
      );
    });

    const withdrawalUsage = dailyTransactions
      .filter((t: any) => t.txnType === 'receive')
      .reduce((sum: number, t: any) => sum + (t.amount || 0), 0);

    const transferUsage = dailyTransactions
      .filter((t: any) => t.txnType === 'send')
      .reduce((sum: number, t: any) => sum + (t.amount || 0), 0);

    return { withdrawalUsage, transferUsage };
  };

  // دالة لتحديث استخدام المحفظة
  const updateWalletUsage = async (walletId: string) => {
    try {
      // جلب المعاملات من Firebase
      const transactions = await transactionService.getByUserId(user?.uid || '');
      const { withdrawalUsage, transferUsage } = await calculateWalletMonthlyUsage(walletId, transactions);
      
      setWallets(prevWallets => 
        prevWallets.map(wallet => {
          if (wallet.id === walletId) {
            const updatedWallet = checkAndResetMonthlyUsage(wallet);
            return {
              ...updatedWallet,
              monthlyWithdrawalUsage: withdrawalUsage,
              monthlyTransferUsage: transferUsage
            };
          }
          return checkAndResetMonthlyUsage(wallet);
        })
      );
    } catch (error) {
      console.error('Error updating wallet usage:', error);
    }
  };

  useEffect(() => {
    // Load wallets from Firebase for the current user
    const loadWallets = async () => {
      // التحقق من وجود المستخدم وحالة المصادقة
      if (!user?.uid) {
        console.log('No user authenticated, skipping wallet loading');
        setIsLoading(false);
        return;
      }
      
      console.log('Loading wallets for user:', user.uid);
      setIsLoading(true);
      
      try {
        // التحقق من حالة المصادقة قبل الطلبات مع انتظار أطول
        const { auth } = await import('@/firebase');
        
        // انتظار حتى يتم تحميل حالة المصادقة بالكامل
        let retryCount = 0;
        const maxRetries = 5;
        
        while (!auth.currentUser && retryCount < maxRetries) {
          console.log(`Waiting for auth state... attempt ${retryCount + 1}`);
          await new Promise(resolve => setTimeout(resolve, 1000));
          retryCount++;
        }
        
        if (!auth.currentUser) {
          console.error('User not authenticated in Firebase Auth after retries');
          throw new Error('not authenticated');
        }
        
        console.log('Firebase Auth User:', auth.currentUser.uid);
        console.log('Context User:', user.uid);
        
        console.log('Fetching wallets from Firebase...');
        const firebaseWallets = await merchantWalletService.getByMerchantId(user.uid);
        console.log('Fetched wallets:', firebaseWallets);
        
        console.log('Fetching transactions from Firebase...');
        const transactions = await transactionService.getByUserId(user.uid);
        console.log('Fetched transactions:', transactions);
        
        const formattedWallets = await Promise.all(firebaseWallets.map(async w => {
          const { withdrawalUsage, transferUsage } = await calculateWalletMonthlyUsage(w.id, transactions);
          const { withdrawalUsage: dailyWithdrawalUsage, transferUsage: dailyTransferUsage } = await calculateWalletDailyUsage(w.id, transactions);
          
          return {
            id: w.id,
            name: w.label || 'محفظة بدون اسم',
            phone: w.msisdn,
            ownerName: w.ownerName,
            nationalId: w.nationalId,
            isDefault: false, // يمكن تحديد المحفظة الافتراضية لاحقاً
            monthlyWithdrawalLimit: w.withdrawLimit || 200000,
            monthlyTransferLimit: w.transferLimit || 200000,
            dailyWithdrawalLimit: (w as any).dailyWithdrawLimit || 100000, // الحد الأقصى للسحب اليومي
            dailyTransferLimit: (w as any).dailyTransferLimit || 60000, // الحد الأقصى للتحويل اليومي
            monthlyWithdrawalUsage: withdrawalUsage,
            monthlyTransferUsage: transferUsage,
            dailyWithdrawalUsage: dailyWithdrawalUsage,
            dailyTransferUsage: dailyTransferUsage,
            lastResetDate: new Date().toISOString().split('T')[0],
            // ضم حقول العمولات الافتراضية إن كانت محفوظة في Firebase
            defaultTransferCommission: (w.defaultTransferCommission != null ? Number(w.defaultTransferCommission) : null),
            defaultWithdrawCommission: (w.defaultWithdrawCommission != null ? Number(w.defaultWithdrawCommission) : null)
          };
        }));
        
        // تطبيق فحص إعادة التعيين على جميع المحافظ
        const walletsWithResetCheck = formattedWallets.map(checkAndResetMonthlyUsage);
        setWallets(walletsWithResetCheck);
        console.log('Wallets loaded successfully:', walletsWithResetCheck);
        
      } catch (error) {
        console.error('Error loading wallets:', error);
        // عدم استخدام بيانات محلية للعرض عند الفشل؛ سيظهر المحتوى فارغًا حتى يعود الاتصال
        setWallets([]);
      } finally {
        setIsLoading(false);
      }
    };

    loadWallets();
  }, [user?.uid]);

  const resetForm = () => {
    setWalletName('');
    setWalletPhone('');
    setWalletOwnerName('');
    setWalletNationalId('');
    setWalletProvider('');
    setMonthlyWithdrawalLimit('200000');
    setMonthlyTransferLimit('200000');
    setDailyWithdrawalLimit('100000');
    setDailyTransferLimit('60000');
    setIsDefault(false);
    setEditingWallet(null);
    setIsAddingWallet(false);
  };

  const startAddWallet = () => {
    setWalletName('');
    setWalletPhone('');
    setWalletOwnerName('');
    setWalletNationalId('');
    setWalletProvider('');
    setMonthlyWithdrawalLimit('200000');
    setMonthlyTransferLimit('200000');
    setDailyWithdrawalLimit('100000');
    setDailyTransferLimit('60000');
    setIsDefault(false);
    setEditingWallet(null);
    setIsAddingWallet(true);
  };

  const startEdit = (wallet: Wallet) => {
    setWalletName(wallet.name);
    setWalletPhone(wallet.phone);
    setWalletOwnerName(wallet.ownerName || '');
    setWalletNationalId(wallet.nationalId || '');
    setMonthlyWithdrawalLimit(wallet.monthlyWithdrawalLimit.toString());
    setMonthlyTransferLimit(wallet.monthlyTransferLimit.toString());
    setDailyWithdrawalLimit(wallet.dailyWithdrawalLimit.toString());
    setDailyTransferLimit(wallet.dailyTransferLimit.toString());
    setIsDefault(wallet.isDefault);
    setEditingWallet(wallet);
    setIsAddingWallet(true);
  };

  const saveWallet = async () => {
    if (!walletName.trim() || !walletPhone.trim() || !walletProvider.trim()) {
      toast({
        title: "خطأ",
        description: "يرجى ملء جميع الحقول المطلوبة",
        variant: "destructive",
      });
      return;
    }

    if (!user?.uid) {
      toast({
        title: "خطأ",
        description: "يجب تسجيل الدخول أولاً",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      if (editingWallet) {
        // Update existing wallet in Firebase
        await merchantWalletService.update(editingWallet.id, {
          label: walletName.trim(),
          msisdn: walletPhone.trim(),
          ownerName: walletOwnerName.trim(),
          nationalId: walletNationalId.trim(),
          withdrawLimit: parseInt(monthlyWithdrawalLimit),
          transferLimit: parseInt(monthlyTransferLimit),
          dailyWithdrawLimit: parseInt(dailyWithdrawalLimit),
          dailyTransferLimit: parseInt(dailyTransferLimit)
        });

        // Sync limits to walletLimits collection for enforcement
        await walletDailyLimitsService.setLimits(editingWallet.id, {
          dailyTransferLimit: parseInt(dailyTransferLimit || '0'),
          dailyWithdrawLimit: parseInt(dailyWithdrawalLimit || '0'),
          monthlyTransferLimit: parseInt(monthlyTransferLimit || '0'),
          monthlyWithdrawLimit: parseInt(monthlyWithdrawalLimit || '0')
        });

        // Update existing wallet in local state
        const updatedWallets = wallets.map(w => 
          w.id === editingWallet.id 
            ? { 
                ...w, 
                name: walletName.trim(), 
                phone: walletPhone.trim(),
                ownerName: walletOwnerName.trim(),
                nationalId: walletNationalId.trim(),
                walletProvider: walletProvider,
                monthlyWithdrawalLimit: parseInt(monthlyWithdrawalLimit),
                monthlyTransferLimit: parseInt(monthlyTransferLimit),
                dailyWithdrawalLimit: parseInt(dailyWithdrawalLimit),
                dailyTransferLimit: parseInt(dailyTransferLimit)
              }
            : w
        );
        
        setWallets(updatedWallets);
        
        // Also save to localStorage for backward compatibility
        localStorage.setItem(getWalletsKey(), JSON.stringify(updatedWallets));
        
        toast({
          title: "تم التحديث",
          description: "تم تحديث المحفظة بنجاح",
        });

      } else {
        // Create new wallet in Firebase
        const newWalletData = {
          merchantUserId: user.uid,
          provider: walletProvider as 'vodafone_cash' | 'etisalat_cash' | 'orange_cash' | 'instapay',
          msisdn: walletPhone.trim(),
          label: walletName.trim(),
          ownerName: walletOwnerName.trim(),
          nationalId: walletNationalId.trim(),
          isActive: true,
          dailyLimit: 200000,
          monthlyLimit: 200000,
          withdrawLimit: parseInt(monthlyWithdrawalLimit),
          transferLimit: parseInt(monthlyTransferLimit),
          dailyTransferLimit: parseInt(dailyTransferLimit),
          dailyWithdrawLimit: parseInt(dailyWithdrawalLimit),
          dailyUsage: 0,
          monthlyUsage: 0,
          transferUsage: 0,
          withdrawUsage: 0
        };

        const newWalletId = await merchantWalletService.create(newWalletData);
        
        // Sync limits to walletLimits collection for enforcement
        await walletDailyLimitsService.setLimits(newWalletId, {
          dailyTransferLimit: parseInt(dailyTransferLimit || '0'),
          dailyWithdrawLimit: parseInt(dailyWithdrawalLimit || '0'),
          monthlyTransferLimit: parseInt(monthlyTransferLimit || '0'),
          monthlyWithdrawLimit: parseInt(monthlyWithdrawalLimit || '0')
        });
        
        // Add to local state with reset usage counters
        const currentDate = new Date().toISOString().split('T')[0]; // YYYY-MM-DD format
        const walletToAdd = {
          id: newWalletId,
          name: walletName.trim(),
          phone: walletPhone.trim(),
          ownerName: walletOwnerName.trim(),
          nationalId: walletNationalId.trim(),
          walletProvider: walletProvider,
          isDefault: isDefault,
          monthlyWithdrawalLimit: parseInt(monthlyWithdrawalLimit),
          monthlyTransferLimit: parseInt(monthlyTransferLimit),
          dailyWithdrawalLimit: parseInt(dailyWithdrawalLimit),
          dailyTransferLimit: parseInt(dailyTransferLimit),
          monthlyWithdrawalUsage: 0, // بدء من الصفر للمحفظة الجديدة
          monthlyTransferUsage: 0, // بدء من الصفر للمحفظة الجديدة
          dailyWithdrawalUsage: 0, // بدء من الصفر للمحفظة الجديدة
          dailyTransferUsage: 0, // بدء من الصفر للمحفظة الجديدة
          lastResetDate: currentDate, // تسجيل تاريخ الإنشاء
          // ضمان الاتساق: تعريف حقول العمولات الافتراضية للمحفظة الجديدة
          defaultTransferCommission: null,
          defaultWithdrawCommission: null
        };
        
        const newWallets = [...wallets, walletToAdd];
        setWallets(newWallets);
        
        // Also save to localStorage for backward compatibility
        localStorage.setItem(getWalletsKey(), JSON.stringify(newWallets));
        
        toast({
          title: "تم الإنشاء",
          description: "تم إنشاء المحفظة بنجاح",
        });
      }
      
      if (onWalletUpdate) {
        onWalletUpdate();
      }

      resetForm();
      setIsAddingWallet(false);
      setEditingWallet(null);
    } catch (error) {
      console.error('Error saving wallet:', error);
      toast({
        title: "خطأ",
        description: "حدث خطأ أثناء حفظ المحفظة",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditWallet = (wallet: Wallet) => {
    setEditingWallet(wallet);
    setWalletName(wallet.name);
    setWalletPhone(wallet.phone);
    setWalletOwnerName(wallet.ownerName || '');
    setWalletNationalId(wallet.nationalId || '');
    setWalletProvider(wallet.walletProvider || '');
    setMonthlyWithdrawalLimit(wallet.monthlyWithdrawalLimit.toString());
    setMonthlyTransferLimit(wallet.monthlyTransferLimit.toString());
    setIsDefault(wallet.isDefault);
    setIsAddingWallet(true);
  };

  const handleUpdateWallet = async () => {
    if (!editingWallet || !walletName || !walletPhone) {
      toast({
        title: "خطأ",
        description: "يرجى ملء جميع الحقول المطلوبة",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      // Update in Firebase
      await merchantWalletService.update(editingWallet.id, {
        label: walletName,
        msisdn: walletPhone,
        ownerName: walletOwnerName.trim(),
        nationalId: walletNationalId.trim(),
        monthlyWithdrawalLimit: parseInt(monthlyWithdrawalLimit),
        monthlyTransferLimit: parseInt(monthlyTransferLimit)
      });

      const updatedWallets = wallets.map(w => 
        w.id === editingWallet.id 
          ? { 
              ...w, 
              name: walletName, 
              phone: walletPhone, 
              ownerName: walletOwnerName.trim(),
              nationalId: walletNationalId.trim(),
              monthlyWithdrawalLimit: parseInt(monthlyWithdrawalLimit), 
              monthlyTransferLimit: parseInt(monthlyTransferLimit) 
            }
          : w
      );

      setWallets(updatedWallets);
      localStorage.setItem(getWalletsKey(), JSON.stringify(updatedWallets));
      setEditingWallet(null);
      resetForm();

      // Call the update callback if provided
      if (onWalletUpdate) {
        onWalletUpdate();
      }

      toast({
        title: "تم تحديث المحفظة",
        description: "تم تحديث بيانات المحفظة بنجاح",
      });
    } catch (error) {
      console.error('Error updating wallet:', error);
      toast({
        title: "خطأ",
        description: "حدث خطأ أثناء تحديث المحفظة",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleDeleteWallet = async (walletId: string) => {
    const walletToDelete = wallets.find(w => w.id === walletId);
    
    if (walletToDelete?.isDefault) {
      toast({
        title: "تحذير",
        description: "لا يمكن حذف المحفظة الافتراضية",
        variant: "destructive",
      });
      return;
    }

    if (!user?.uid) {
      toast({
        title: "خطأ",
        description: "يجب تسجيل الدخول أولاً",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      // Delete from Firebase
      await merchantWalletService.delete(walletId);
      
      // Update local state
      const updatedWallets = wallets.filter(w => w.id !== walletId);
      setWallets(updatedWallets);
      
      // Also update localStorage for backward compatibility
      localStorage.setItem(getWalletsKey(), JSON.stringify(updatedWallets));
      
      if (onWalletUpdate) {
        onWalletUpdate();
      }

      toast({
        title: "تم الحذف",
        description: "تم حذف المحفظة بنجاح",
      });
    } catch (error) {
      console.error('Error deleting wallet:', error);
      toast({
        title: "خطأ",
        description: "حدث خطأ أثناء حذف المحفظة",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleSetDefault = (walletId: string) => {
    const updatedWallets = wallets.map(w => ({
      ...w,
      isDefault: w.id === walletId
    }));

    setWallets(updatedWallets);
    localStorage.setItem(getWalletsKey(), JSON.stringify(updatedWallets));
    
    // Call the update callback if provided
    if (onWalletUpdate) {
      onWalletUpdate();
    }

    toast({
      title: "تم تحديث المحفظة الافتراضية",
      description: "تم تعيين المحفظة كافتراضية بنجاح",
    });
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto">
        <DialogHeader className="pb-2">
          <DialogTitle className="flex items-center gap-1 text-sm">
            <Wallet className="h-3 w-3" />
            إدارة المحافظ
          </DialogTitle>
        </DialogHeader>

        <div className="space-y-2">
          {/* Add New Wallet Button */}
          <div className="flex justify-between items-center">
            <h3 className="text-sm font-semibold">المحافظ المتاحة ({wallets.length})</h3>
            <div className="flex gap-2">
              <Button
                onClick={() => {
                  // اجبار ادخال الرقم السري الرئيسي قبل الاطلاع على المحافظ
                  setIsMasterPinDialogOpen(true);
                }}
                className="flex items-center gap-1 h-6 px-2 text-xs"
                size="sm"
                variant="outline"
              >
                <Eye className="h-3 w-3" />
                الاطلاع على المحافظ
              </Button>
              <Button
                onClick={() => {
                  startAddWallet();
                }}
                className="flex items-center gap-1 h-6 px-2 text-xs"
                size="sm"
              >
                <Plus className="h-3 w-3" />
                إضافة محفظة جديدة
              </Button>
            </div>
          </div>

          {/* Add/Edit Wallet Form */}
          {(isAddingWallet || editingWallet) && (
            <Card className="border-2 border-blue-200 bg-blue-50">
              <CardHeader className="pb-1">
                <CardTitle className="text-sm">
                  {editingWallet ? 'تعديل المحفظة' : 'إضافة محفظة جديدة'}
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-2 pt-1">
                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    شركة المحفظة
                  </label>
                  <div className="flex gap-1 items-center">
                    <Select value={walletProvider} onValueChange={setWalletProvider}>
                      <SelectTrigger className="h-6 text-xs flex-1">
                        <SelectValue placeholder="اختر شركة المحفظة" />
                      </SelectTrigger>
                      <SelectContent>
                        {walletProviders.map((provider) => (
                          <SelectItem key={provider.id} value={provider.id} className="text-xs">
                            <div className="flex items-center justify-between w-full">
                              <span>{provider.name}</span>
                            </div>
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    اسم المحفظة
                  </label>
                  <Input
                    value={walletName}
                    onChange={(e) => setWalletName(e.target.value)}
                    placeholder="مثال: محفظة العمل"
                    className="h-6 text-xs"
                  />
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    رقم الهاتف
                  </label>
                  <div className="relative">
                    <Phone className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-gray-400" />
                    <Input
                      value={walletPhone}
                      onChange={(e) => setWalletPhone(e.target.value)}
                      placeholder="01030302038"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    اسم مالك المحفظة
                  </label>
                  <div className="relative">
                    <User className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-gray-400" />
                    <Input
                      value={walletOwnerName}
                      onChange={(e) => setWalletOwnerName(e.target.value)}
                      placeholder="أدخل اسم مالك المحفظة"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    الرقم القومي للخط
                  </label>
                  <div className="relative">
                    <IdCard className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-gray-400" />
                    <Input
                      value={walletNationalId}
                      onChange={(e) => setWalletNationalId(e.target.value)}
                      placeholder="29012345678901"
                      maxLength={14}
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    الحد الشهري للسحب (جنيه)
                  </label>
                  <div className="relative">
                    <DollarSign className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-gray-400" />
                    <Input
                      type="number"
                      value={monthlyWithdrawalLimit}
                      onChange={(e) => setMonthlyWithdrawalLimit(e.target.value)}
                      placeholder="200000"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    الحد الشهري للتحويل (جنيه)
                  </label>
                  <div className="relative">
                    <DollarSign className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-gray-400" />
                    <Input
                      type="number"
                      value={monthlyTransferLimit}
                      onChange={(e) => setMonthlyTransferLimit(e.target.value)}
                      placeholder="200000"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    الحد اليومي للسحب (جنيه)
                  </label>
                  <div className="relative">
                    <TrendingDown className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-red-400" />
                    <Input
                      type="number"
                      value={dailyWithdrawalLimit}
                      onChange={(e) => setDailyWithdrawalLimit(e.target.value)}
                      placeholder="100000"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div>
                  <label className="text-xs font-medium text-gray-700 mb-1 block">
                    الحد اليومي للتحويل (جنيه)
                  </label>
                  <div className="relative">
                    <TrendingUp className="absolute left-2 top-1/2 transform -translate-y-1/2 h-3 w-3 text-blue-400" />
                    <Input
                      type="number"
                      value={dailyTransferLimit}
                      onChange={(e) => setDailyTransferLimit(e.target.value)}
                      placeholder="60000"
                      className="pl-6 h-6 text-xs"
                    />
                  </div>
                </div>

                <div className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id="isDefault"
                    checked={isDefault}
                    onChange={(e) => setIsDefault(e.target.checked)}
                    className="rounded w-3 h-3"
                  />
                  <label htmlFor="isDefault" className="text-xs font-medium text-gray-700">
                    تعيين كمحفظة افتراضية
                  </label>
                </div>

                {/* Commission Settings Section */}
                <div className="border-t pt-2 mt-2">
                  <div className="flex items-center gap-1 mb-2">
                    <Settings className="h-3 w-3 text-gray-600" />
                    <label className="text-xs font-medium text-gray-700">
                      إعدادات العمولة
                    </label>
                  </div>
                </div>

                <div className="flex gap-1">
                  <Button
                    onClick={saveWallet}
                    className="flex-1 h-6 text-xs"
                    size="sm"
                  >
                    {editingWallet ? 'تحديث المحفظة' : 'إضافة المحفظة'}
                  </Button>
                  <Button
                    variant="outline"
                    onClick={resetForm}
                    className="h-6 text-xs"
                    size="sm"
                  >
                    إلغاء
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Wallets List */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
            {wallets.map((wallet) => (
              <Card key={wallet.id} className={`${wallet.isDefault ? 'border-green-300 bg-green-50' : ''}`}>
                <CardHeader className="pb-1">
                  <div className="flex items-center justify-between">
                    <CardTitle className="text-sm flex items-center gap-1">
                      <Wallet className="h-3 w-3" />
                      {wallet.name}
                    </CardTitle>
                    {wallet.isDefault && (
                      <Badge variant="default" className="bg-green-600 text-xs px-1 py-0">
                        افتراضية
                      </Badge>
                    )}
                  </div>
                </CardHeader>
                <CardContent className="space-y-1 pt-0">
                  <div className="flex items-center gap-1">
                    <Phone className="h-3 w-3 text-gray-500" />
                    <span className="text-xs">{wallet.phone}</span>
                  </div>
                  {wallet.walletProvider && (
                    <div className="text-xs text-blue-600 mt-1">
                      {walletProviders.find(p => p.id === wallet.walletProvider)?.name || wallet.walletProvider}
                    </div>
                  )}
                  
                  {/* شريط تقدم الحدود الشهرية واليومية لكل محفظة */}
                  <div className="bg-gradient-to-r from-gray-50 to-gray-100 p-2 rounded-md border border-gray-200 shadow-sm space-y-2">
                    {/* الحدود الشهرية */}
                    <div>
                      <div className="text-xs font-medium text-gray-600 mb-1">الحدود الشهرية</div>
                      <div className="w-full bg-gray-200 rounded-full h-1 overflow-hidden flex">
                        {/* نصف السحب */}
                        <div className="w-1/2 bg-red-200 relative">
                          <div 
                            className="bg-gradient-to-r from-red-400 to-red-600 h-1 transition-all duration-700 ease-out shadow-sm"
                            style={{ 
                              width: `${Math.min(((wallet.monthlyWithdrawalUsage || 0) / wallet.monthlyWithdrawalLimit) * 100, 100)}%` 
                            }}
                          ></div>
                        </div>
                        {/* نصف التحويل */}
                        <div className="w-1/2 bg-blue-200 relative">
                          <div 
                            className="bg-gradient-to-r from-blue-400 to-blue-600 h-1 transition-all duration-700 ease-out shadow-sm"
                            style={{ 
                              width: `${Math.min(((wallet.monthlyTransferUsage || 0) / wallet.monthlyTransferLimit) * 100, 100)}%` 
                            }}
                          ></div>
                        </div>
                      </div>
                      
                      {/* تفاصيل المبالغ المتبقية الشهرية */}
                      <div className="flex justify-between items-center mt-0.5">
                        <div className="text-xs text-red-600 font-medium">
                          متبقي سحب: {Math.max(wallet.monthlyWithdrawalLimit - (wallet.monthlyWithdrawalUsage || 0), 0)} جنيه
                        </div>
                        <div className="text-xs text-blue-600 font-medium">
                          متبقي تحويل: {Math.max(wallet.monthlyTransferLimit - (wallet.monthlyTransferUsage || 0), 0)} جنيه
                        </div>
                      </div>
                    </div>

                    {/* الحدود اليومية */}
                    {(
                      <div>
                        <div className="text-xs font-medium text-gray-600 mb-1">الحدود اليومية</div>
                        <div className="w-full bg-gray-200 rounded-full h-1 overflow-hidden flex">
                          {/* نصف السحب اليومي */}
                          <div className="w-1/2 bg-orange-200 relative">
                            <div 
                              className="bg-gradient-to-r from-orange-400 to-orange-600 h-1 transition-all duration-700 ease-out shadow-sm"
                              style={{ 
                                width: `${Math.min(((wallet.dailyWithdrawalUsage || 0) / wallet.dailyWithdrawalLimit) * 100, 100)}%` 
                              }}
                            ></div>
                          </div>
                          {/* نصف التحويل اليومي */}
                          <div className="w-1/2 bg-green-200 relative">
                            <div 
                              className="bg-gradient-to-r from-green-400 to-green-600 h-1 transition-all duration-700 ease-out shadow-sm"
                              style={{ 
                                width: `${Math.min(((wallet.dailyTransferUsage || 0) / wallet.dailyTransferLimit) * 100, 100)}%` 
                              }}
                            ></div>
                          </div>
                        </div>
                        
                        {/* تفاصيل المبالغ المتبقية اليومية */}
                        <div className="flex justify بين items-center mt-0.5">
                          <div className="text-xs text-orange-600 font-medium">
                            متبقي سحب يومي: {Math.max(wallet.dailyWithdrawalLimit - (wallet.dailyWithdrawalUsage || 0), 0)} جنيه
                          </div>
                          <div className="text-xs text-green-600 font-medium">
                            متبقي تحويل يومي: {Math.max(wallet.dailyTransferLimit - (wallet.dailyTransferUsage || 0), 0)} جنيه
                          </div>
                        </div>
                      </div>
                    )}
                  </div>

                  <div className="flex gap-1 pt-1">
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => handleEditWallet(wallet)}
                      className="flex-1 h-5 text-xs px-1"
                    >
                      <Edit className="h-2 w-2 mr-0.5" />
                      تعديل
                    </Button>
                    
                    {!wallet.isDefault && (
                      <>
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => handleSetDefault(wallet.id)}
                          className="flex-1 h-5 text-xs px-1"
                        >
                          جعلها افتراضية
                        </Button>
                        <Button
                          size="sm"
                          variant="destructive"
                          onClick={() => handleDeleteWallet(wallet.id)}
                          className="h-5 px-1"
                        >
                          <Trash2 className="h-2 w-2" />
                        </Button>
                      </>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>

          {wallets.length === 0 && (
            <div className="text-center py-4 text-gray-500 text-xs">
              لا توجد محافظ متاحة. قم بإضافة محفظة جديدة للبدء.
            </div>
          )}
        </div>

        <Button
          variant="ghost"
          size="icon"
          className="fixed bottom-6 right-6 h-14 w-14 rounded-full bg-red-600 text-white shadow-lg hover:bg-red-700 hover:scale-105 transition-all duration-300 z-[60]"
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
            onClose();
          }}
        >
          <ArrowLeft className="h-6 w-6" />
        </Button>
      </DialogContent>

      {/* رقم سري رئيسي قبل فتح عرض المحافظ */}
      <MasterPinDialog
        open={isMasterPinDialogOpen}
        onOpenChange={setIsMasterPinDialogOpen}
        mode="verify"
        title="إدخال الرقم السري الرئيسي"
        description="لا يمكن الاطلاع على المحافظ إلا بإدخال الرقم السري الرئيسي"
        userId={user?.uid}
        onSuccess={() => {
          setIsMasterPinDialogOpen(false);
          setShowWalletsView(true);
        }}
      />

      {/* صفحة عرض المحافظ */}
      <WalletsViewPage
        isOpen={showWalletsView}
        onClose={() => setShowWalletsView(false)}
        onWalletSelect={(wallet) => {
          setSelectedWallet(wallet);
          setShowWalletsView(false);
        }}
        onEditWallet={(wallet) => {
          handleEditWallet(wallet);
          setShowWalletsView(false);
        }}
        onDeleteWallet={async (walletId) => {
          // حذف نهائي بدون تأكيد
          try {
            await merchantWalletService.delete(walletId);
            // حدّث الحالة المحلية وقم بمزامنة localStorage لضمان انعكاس الحذف في القوائم
            setWallets(prev => {
              const updated = prev.filter(w => w.id !== walletId);
              try {
                localStorage.setItem(getWalletsKey(), JSON.stringify(updated));
              } catch (e) {
                console.error('Failed to update localStorage wallets after delete:', e);
              }
              return updated;
            });

            // إعلام الصفحة/المكوّن الأب لتحديث القوائم (مثل صفحة لوحة المستخدم)
            if (onWalletUpdate) {
              try {
                await onWalletUpdate();
              } catch (e) {
                console.error('onWalletUpdate callback failed:', e);
              }
            }
          } catch (err) {
            console.error('Error deleting wallet:', err);
          }
        }}
      />

      {/* صفحة تفاصيل المحفظة */}
      <WalletDetailsPage
        wallet={selectedWallet}
        isOpen={!!selectedWallet}
        onClose={() => setSelectedWallet(null)}
        onBack={() => {
          setSelectedWallet(null);
          setShowWalletsView(true);
        }}
      />
    </Dialog>
  );
};

export default WalletManagement;
